import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [notificationEnabled, setNotificationEnabled] = useState(false);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const url_string = window.location.href;
	const url = new URL(url_string);
	const vk_are_notifications_enabled = url.searchParams.get("vk_are_notifications_enabled");

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		// TODO включалка нотификаций
		if (!+vk_are_notifications_enabled) {
			// setTimeout(() => {
			const res = connect.send("VKWebAppAllowNotifications", {})
			console.log(111, res)
				// .then(() => {
				// 	setNotificationEnabled(true)
				// })
			// connect.send("VKWebAppDenyNotifications", {});
            // }, 60000)
		}
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			console.log(user)
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};
	const notificationSwitch = () => {
		setNotificationEnabled(!notificationEnabled)
		if (notificationEnabled) {
			connect.send("VKWebAppAllowNotifications", {});
		} else {
			connect.send("VKWebAppDenyNotifications", {});
		}
		// connect.send("VKWebAppAllowNotifications", {});
	};
	if (fetchedUser && fetchedUser.id) {
		fetch('https://glavclub.com/vk/KwgG259rqFt9gdDNpj8f/' + fetchedUser.id)
			.then(response => response.json())
			.then(response => {
				console.log(response)
			});
	}


	return (
		<View activePanel={activePanel} >
			<Home id='home'
				  fetchedUser={fetchedUser}
				  go={go}
				  notificationEnabled={vk_are_notifications_enabled ? +vk_are_notifications_enabled : false}
				  notificationSwitch={notificationSwitch}
			/>
		</View>
	);
}

export default App;

